<template>
<div class="row" v-if="ready">
      <div class="col-12">
        <form id='general_req_form' enctype="multipart/form-data" class="form-horizontal" data-vv-scope="form1"  @submit.prevent="handleUpdate('form1')">
        <div class="card">
          <div class="card-header">
            <strong>General Request Form (Full Control)</strong>
          </div>            
          <div class="card-body card-block">
            <div v-if="imgbox_opener" class="col-12 " style='position:relative;z-index:10'>
              <div class="cross_box">
                <i class="fa fa-window-close" aria-hidden="true" style='margin-left:auto;margin-top:0.25rem;margin-right:0.5rem;cursor:pointer' @click="close_overlay"></i>
              </div>
              <div class="col-12 imgbox_holder" style='position:absolute;left:0;top:0;margin-left:auto;margin-top:auto;padding:0 0 0 0'>
                <img :src="imgbox" style='width:100%;height:auto'/>
              </div>
            </div>
              <div class="row form-group">
                <div class="col col-md-3"><label class=" form-control-label">Requester</label>
                </div>
                <div class="col-12 col-md-9">
                  <p class="form-control-static">{{ req.name }}</p>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="select" class=" form-control-label">Request For</label></div>
                <div class="col-12 col-md-9">
                  <select name="Request_for" id="rf" class="form-control select" v-model="req.req_for" required>
                        <option v-for="dp in reqdp">{{ dp }}</option>
                    </select>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="select" class=" form-control-label">Category</label></div>
                <div class="col-12 col-md-9">
                  <select name="Request_for" id="rf" class="form-control select" v-model="req.cat" required>
                        <option v-for="cat in category">{{ cat }}</option>
                    </select>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="select" class=" form-control-label">Status</label></div>
                <div class="col-12 col-md-9">
                  <select name="Request_for" id="rf" class="form-control select" v-model="req.stat" required>
                        <option v-for="stat in issue_stat">{{ stat }}</option>
                  </select>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Description" class=" form-control-label">Description</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <textarea id ="Description" name="Description" rows="3" cols="80" placeholder="Enter description" maxlength="250" class="form-control" required v-model="req.descript" v-validate="'required'" :class="{ 'is-invalid': submitted && errors.has('Description') }"></textarea>
                    <div v-if="submitted && errors.has('Description')" class="invalid-feedback">{{ errors.first('Description') }}</div>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Location" class=" form-control-label">Location</label></div>
                <div class="col-12 col-md-9">
                    <input type="text" id="Location" name="Location" placeholder="Enter Location" maxlength="150"  class="form-control" v-model="req.location" required v-validate="'required'" :class="{ 'is-invalid': submitted && errors.has('Location') }"/>
                    <div v-if="submitted && errors.has('Location')" class="invalid-feedback">{{ errors.first('Location') }}</div>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="handler" class=" form-control-label">Assigned To</label></div>
                <div class="col-12 col-md-9">
                    <input type="text" id="handler" name="handler" placeholder="Seperate multiple input with ,"  class="form-control" v-model="req.handler" required @keyup="search_record($event)"/>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Due_date" class=" form-control-label">Due Date</label></div>
                <div class="col-12 col-md-9">
                    <input type='hidden' id='date_limit' name='date_limit' value=''/>
                    <input type="date" id="Due_date" name="Due_date" class="form-control" data-date-split-input="true" id="min_date" required v-model="req.dd" />    
                    <input type="hidden" v-model="req.name"/>            
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Due_time" class=" form-control-label" >Due Time</label></div>
                <div class="col-12 col-md-9">
                   <input type="time" name="Due_time" value="00:00" class="form-control" v-model="req.dt"/>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Due_time" class=" form-control-label" >Remarks</label></div>
                <div class="col-12 col-md-9">
                   <input type="text" name="remarks" class="form-control" v-model="req.remarks"/>
                </div>
              </div>
               <div class="row form-group" v-if="req.pdf!=''"> <!--hall ir03-->
                <div class="col col-md-3"><label for="Due_time" class=" form-control-label" >Hall Request Form</label></div>
                <div class="col-12 col-md-9">
                   <a v-bind:href="'src/RequestPortal'+ req.pdf" target="_blank"><u>View Application Form</u></a>
                </div>
              </div>
              <div class="row form-group" style="height:auto" v-if="req.imgstat=='true'">
                <div class="col col-md-3"><label class=" form-control-label" >Supporting Image</label></div>
                <div class="col-12 col-md-9">
                   <button type="button" value="View Image" @click="viewimg" ref="showimg" style="width:8em;height:2.5em;" class="form-control" v-if="notshow">View Image</button>
                     <div class='imgcontainer' style="height:13em">
                       <img width="50%" class="image" :src="imgbox"/>
                      <div class="middle">
                        <div class="flex" style='display:flex;flex-direction:row'>
                          <div class="text" @click="updateimg">Update</div>
                          <div class="text" @click="open_viewimg" style='margin-left:1rem'>View</div>
                        <input type="file" id='upload_img' class="form-control-button" accept="image/jpeg" ref="multimedia_info" style='display:none' @change="addfile()"/>
                      </div>
                    </div>
                </div>
              </div>
              <div class="row form-group">
                <div class="col-12 col-md-9">
                   {{return_msg}}
                </div>
              </div>
          </div>                     
      </div>
        <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm">
              <i class="fa fa-paper-plane"></i> Update
            </button>
            <button type="button" class="btn btn-danger btn-sm" onclick="window.history.go(-1); return false;">
              <i class="fa fa-undo"></i> Return
            </button>
        </div>
    </form>
      <div class="col-xs-3 col-md-3" style='position:fixed;right:250px;top:250px;box-shadow: #888888;'>
           <v-data-table
                :headers="headers"
                :items="recep_data"
                :search="search"
                class="elevation-1"
                :rows-per-page-items="[10]"
                v-if="hv_record"
                >
            <template v-slot:items="props">
                <tr v-on:click="select_user(props.item.name)" style='cursor:pointer'>
                    <td>Select</td>
                    <td>{{ props.item.name }}</td>
                </tr>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table>  
        </div>
    </div>   
</template>
<script>
export default {
    data(){
        return{
            req:{
                id:"",
                req_for:"",
                cat:"",
                descript:"",
                location:"",
                dd:"",
                dt:"",
                remarks:"",
                name:"",
                handler:"",
                img:'',
                imgstat:false,
                pdf:''
            },
            ready:false,
            imgbox_opener:false,
            hv_record:false,
            notshow:true,
            fetchdata:[],
            reqdp:['IT & Resources','Academic'], 
            category:['Service','Maintenance'], 
            issue_stat:['Not Started','In Progress','Completed','Waiting on something else','Cancelled','Rejected'], 
            req_name:"",
            return_msg:"",
            imgbox:"",
            ready_to_show:false,
            pdf_path:'',
            headers: [
            { text: 'Action',value :'action',sortable: false},
            { text: 'Name',value:'name',sortable: false},
            ],
            recep_data:[],
        }
    },
    async created(){
        var id = document.getElementById('tmp_id').value;
        var formData = new FormData();
        formData.append('id', id);
        await axios.post('RequestPortal/backend/edit_form.php',formData)
          .then(
            response=>{
              this.fetchdata = response.data
              this.req.id = this.fetchdata[0].item_id
              this.req.req_for = this.fetchdata[0].Request_for
              this.req.cat = this.fetchdata[0].Category
              this.req.descript = this.fetchdata[0].Description
              this.req.location = this.fetchdata[0].Location
              this.req.dd = this.fetchdata[0].Due_date
              this.req.dt = this.fetchdata[0].Due_time
              this.req.name = this.fetchdata[0].Req_Name
              this.req.remarks = this.fetchdata[0].Remarks
              this.req.handler = this.fetchdata[0].Handler
              this.req.stat = this.fetchdata[0].Status
              this.req.imgstat = this.fetchdata[0].imgstat
              this.req.pdf = this.fetchdata[0].pdf_path
            }
          )
          this.ready = true;            
    },
    async mounted(){
        window.onbeforeunload = function (e) {
            e = e || window.event;
            if (e) {
                e.returnValue = 'Changes you made may not be saved.';
            }
            return 'Changes you made may not be saved.';
        }
        var id = document.getElementById('tmp_id').value;
        var formData = new FormData();
        formData.append('id',id);
        await axios.post('RequestPortal/backend/dlimg.php',formData)
        .then(
        (response)=>{
          this.imgbox = response.data;         
        }
      )    
      this.notshow=false;   
    },
    destroyed(){
      window.onbeforeunload=null;
    },
    methods:{
      search_record(event){
            if(!(event.keyCode==27)){
                var formData = new FormData();
                formData.append('key', this.req.handler);
                axios.post('RequestPortal/backend/search_user_list.php',formData)
                .then(
                    (response)=>{
                        if(response.data == "Empty"){
                        this.hv_record=false; 
                        } else {
                            this.recep_data = response.data;
                            this.recep_data.username = response.data.username;
                            this.recep_data.name = response.data.name;
                            this.hv_record=true;
                        }
                    }); 
            }
        },
        select_user(event){
            this.req.handler = event;
            this.hv_record = false;
        },
        updateimg(){
          document.getElementById('upload_img').click();
        },
        handleUpdate(scope){
          var formData = new FormData();
          formData.append('modid', this.req.id);
          formData.append('Request_for', this.req.req_for);
          formData.append('Status', this.req.stat);
          formData.append('Requester', this.req.name);
          formData.append('Handler', this.req.handler);
          formData.append('Description', this.req.descript);
          formData.append('Location', this.req.location);
          formData.append('Due_date', this.req.dd);
          formData.append('Due_time', this.req.dt);
          formData.append('remarks', this.req.remarks);
          formData.append('Category', this.req.cat);
          if(this.files!=undefined){
            formData.append('img', this.files);
          }       
          axios.post('RequestPortal/backend/mod_form.php',formData)
          .then(
            response=>{
              this.return_msg = response.data
              if(this.return_msg=="success"){
                var ret =  document.getElementById('entry_type').value;
                if((ret!='')||(ret!=null)||ret!=undefined){
                  this.$router.push(ret);
                } else {
                  this.$router.push('IR');
                }
            }           
        }  
      )
    },
    addfile(){
      this.files =  this.$refs.multimedia_info.files[0];
      var src = URL.createObjectURL(this.files);
      this.imgbox = src;
    },
    open_viewimg(){         
      this.imgbox_opener = !this.imgbox_opener;
    },
    close_overlay(){
      this.imgbox_opener = false;
    },
    viewimg(event){
      var formData = new FormData();
      formData.append('id', this.fetchdata[0].item_id);
      axios.post('RequestPortal/backend/dlimg.php',formData)
      .then(
        (response)=>{
          this.imgbox = response.data;
        }
      )
      this.notshow=false;
    }
}
}
</script>
<style scoped>
.imgcontainer{
  position: relative;
}
.image {
  opacity: 1;
  display: block;
  width: auto;
  height: 13em;
  transition: .5s ease;
  backface-visibility: hidden;
  margin-left:auto;
  margin-right:auto;
}
.middle {
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}
.text {
  background-color: rgb(238,28,37);
  color: white;
  cursor:pointer;
  font-size: 16px;
  padding: 16px 32px;
}
.imgcontainer:hover .image {
  opacity: 0.3;
}
.imgcontainer:hover .middle {
  opacity: 1;
}
.cross_box{
    position:absolute;
    right:0;
    top:0;
    z-index:100;
}
.fa-window-close{
    transition: all 0.15s ease-in-out;
    color:rgba(0, 0, 0, 0.55);
}
.fa-window-close:hover{
    color:rgba(0, 0, 0, 1);
}
.imgbox_holder{
  border: 1px solid;
  box-shadow: 2px 2px #888888;
}
</style>


